.container {
  padding: 40px;
  min-height: 500px;
}

.containerInfo {
  display: flex;
  align-items: center;
  padding: 40px;
  min-height: 500px;
  justify-content: space-around;
}

.orders {
  align-self: flex-start;
  display: flex;
  flex-direction: column;

  &__top {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
  }
}

.order {
  margin-bottom: 20px;
  &__title {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 20px;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
  }
}

.info {
  max-width: 315px;
  img {
    margin-bottom: 20px;
  }
  h3 {
    margin-bottom: 10;
  }
  p {
    margin-bottom: 50px;
  }
}
